import React from "react"
import { Link } from "gatsby"
import { graphql } from "gatsby"

import Layout from "../components/layout/custom/layout"
// import Image from "../components/image"
import SEO from "../components/layout/custom/seo"

import logo from "../images/Tekken_series_logo_as_of_2012.gif"
function importAll(r) {
  return r.keys().map(r)
}

// const playersImage = importAll(require.context('../images/players', false, /\.(png|jpe?g|svg)$/));

export const query = graphql`
  {
    allPlayersCsv {
      edges {
        node {
          id
          name
          image
          desc
          first_appearence
          created_by
          occupation
          fighting_style
          nationality
          created_at
        }
      }
    }
  }
`

const PlayerDetailsPage = ({ data }) => {
  const players = data.allPlayersCsv.edges

  let [player, setPlayer] = React.useState(undefined);
  let [img, setImg] = React.useState(undefined);

  React.useEffect(() => {

    let href = window.location.href
  
    let id = href.split("?id=")[1]
  
     setPlayer( players.filter(player => player.node.id === id)[0])
  
    console.log("hihi")
    console.log("sdsd")
    
  if(player){

    setImg( require("../images/players/" + player.node.image))
  }

  })



  return (
    <Layout>
      <SEO title="Home" />

      <main id="main">
        <br />
        <br />

        {player &&
        <section id="about" className="about" style={{ minHeight: "600px" }}>
          <div className="container">
            <div
              className="section-title aos-init aos-animate"
              data-aos="zoom-out"
            >
              <h2>About</h2>
              <p>{player.node.name}</p>

            </div>

            <div
              className="row content aos-init aos-animate"
              data-aos="fade-up"
            >
              <div className="col-lg-6">
                <p>
                 {player.node.desc}
                </p>
               
              </div>
              <div className="col-lg-6 pt-4 pt-lg-0">
                

                <img src={img}  />
                
              
                <br/>
                <Link to="/" className="btn-learn-more">
                  Home
                </Link>

              </div>
            </div>
          </div>
        </section>
}
      </main>
      {/* <!-- End #main --> */}
    </Layout>
  )
}
export default PlayerDetailsPage
